<template>
  <div>
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="backTo()">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">辅助决策</span>
    </mu-appbar>-->
    <van-nav-bar
      title="辅助决策"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="0"
      :border="false"
    >
      <!-- <van-icon name="search" slot="right" /> -->
    </van-nav-bar>
    <div class="containerAssit">
      <div :style="height" v-for="(item,index) in menuList" :key="index">
        <div class="btn" @click="toNavgation(item.name)" :style="width">
          <img :src="item.img" alt />
          <span>{{item.name}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";
const width = (document.documentElement.clientWidth - 40) / 3;
export default {
  data() {
    return {
      height: "",
      menuList: [
        { name: "水环境质量评价", img: "static/imgs/ranking.png" },
        { name: "水环境质量排名", img: "static/imgs/face.png" },
        { name: "其他", img: "static/imgs/historyAA.png" }
      ],
      width: "width:" + width + "px;height:" + width + "px"
    };
  },
  methods: {
    backTo() {
      this.$router.go(-1);
    },
    toNavgation(e) {
      console.log(e);
      var router = "";
      switch (e) {
        case "水环境质量评价":
          router = "/waterPollution";
          break;

        case "水环境质量排名":
          router = "/ranking";
          break;
        case "历史数据":
          router = "/Realdata";
          break;
        default:
          Toast("正在测试，稍后推出...");
          break;
      }
      this.$router.push(router);
    }
  },
  mounted() {
    console.log(document.documentElement.clientHeight);
    this.height =
      "height:" + (document.documentElement.clientHeight - (56 + 41)) + "px";

    // console.log(this.height);
  }
};
</script>
<style scoped>
.containerAssit {
  display: flex;
  flex-direction: row;
  flex: 1;
  padding: 10px;
  /* padding-top: 10%; */
  justify-content: space-between;
  background: rgb(247, 247, 247);
  height: 100%;
}
.btn {
  /* margin-top: 20%; */
  border: 1px solid rgb(236, 235, 235);
  width: 120px;
  height: 120px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  /* font-size: 16px; */
  /* color: white; */
  /* box-shadow: 5px 5px 5px #ccc; */
  /* padding: 10px; */
  flex-direction: column;
  font-size: 12px;
  /* margin-right: 10px; */
}
.btn span {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
}
.btn img {
  width: 60px;
  height: 60px;
}
</style>


